@import url(https://fonts.googleapis.com/css2?family=Exo:wght@400;500&display=swap);
:root {
  --color-hue: 160;
  --color-primary: hsl(var(--color-hue), 70%, 60%);
  --color-primary-two: hsl(var(--color-hue), 30%, 5%);
  --color-primary-light: hsl(var(--color-hue), 90%, 85%);
  --color-title: hsl(var(--color-hue), 8%, 95%);
  --color-text: hsl(var(--color-hue), 8%, 75%);
  --color-text-light: hsl(var(--color-hue), 8%, 65%);
  --color-input: hsl(var(--color-hue), 6%, 15%);
  --color-body: hsl(var(--color-hue), 4%, 10%);
  --color-container: hsl(var(--color-hue), 15%, 20%);

  --font-body: "Exo", sans-serif;

  --width-desktop: 1200px;
}

* {
  box-sizing: border-box;
}

body {
  color: hsl(160, 8%, 75%);
  color: var(--color-text);
  background-color: hsl(160, 4%, 10%);
  background-color: var(--color-body);
  font-size: 1rem;
  font-family: "Exo", sans-serif;
  font-family: var(--font-body);
  margin: 0;
}

body.day-mode {
  --color-primary-two: hsl(var(--color-hue), 55%, 55%);
  --color-title: hsl(var(--color-hue), 8%, 15%);
  --color-text: hsl(var(--color-hue), 8%, 45%);
  --color-input: hsl(var(--color-hue), 30%, 92%);
  --color-body: hsl(var(--color-hue), 30%, 98%);
  --color-container: hsl(var(--color-hue), 50%, 99%);
}

h1,
h2,
h3 {
  color: hsl(160, 8%, 95%);
  color: var(--color-title);
  align-items: center;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth !important;
}

.section {
  display: flex;
  flex-direction: column;
  padding: 2rem 0;
  min-height: 100vh;
  max-width: var(--width-desktop);
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 2rem;
  border: none;
  font-family: var(--body-font);
  font-weight: 600;
  cursor: pointer;
  transition: 0.4s;
  width: 100%;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.btn:hover {
  opacity: 0.9;
  transform: translateY(-3px);
}

.btn:disabled {
  cursor: not-allowed;
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
  background: var(--color-body);
}

.btn-primary {
  color: black;
  background-color: var(--color-primary);
  position: relative;
}

.btn-outline {
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
  background-color: var(--color-body);
  position: relative;
}

.btn-primary:not([disabled]):before,
.btn-outline::before {
  position: absolute;
  display: inline-block;
  content: "";
  width: 100%;
  height: 100%;
  top: -1px;
  left: -1px;
  z-index: -1;
  transition: 0.25s;
}

.btn-primary:not([disabled]):before {
  background: var(--color-primary);
}

.btn-outline::before {
  border: 1px solid var(--color-primary);
}

.btn-primary:hover::before,
.btn-outline:hover::before {
  transform: scale(1.07, 1.5);
  opacity: 0;
}

.about-item {
  background: var(--color-input);
  flex: 1 1;
  height: 100%;
  margin: 0 0 1rem;
  padding: 2rem;
  transition: 0.25s ease;
}

.about:hover .about-item {
  opacity: 0.5;
}

.about .about-item:hover {
  opacity: 1;
  transform: scale(1.1);
  cursor: default;
}

.about-item:hover .card-title::before {
  width: 3.5rem;
}

.about-item-content {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  margin: 1rem 0 0;
  font-weight: 600;
}

.about-item-content-wrapper {
  flex: 1 1;
  min-width: 10rem;
}

.about-item-content-date {
  font-weight: 400;
  margin: 0 2rem 0 0;
  text-transform: uppercase;
  font-size: 0.75rem;
  color: var(--color-text);
  letter-spacing: 2px;
  min-width: 10rem;
}

.about-item-content-headline {
  margin: 0;
  color: var(--color-title);
  font-size: 1.25rem;
  letter-spacing: 1px;
}

.about-item-content-description {
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 500;
  font-size: 0.925rem;
  margin: 0.25rem 0;
  color: var(--color-primary);
}

.about-item-content-location {
  margin: 0;
  font-weight: 400;
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

#about .btn-outline {
  margin-top: 2rem;
  width: 100%;
  text-align: center;
}

.service-cards:hover .service-card {
  opacity: 0.5;
}

.service-cards .service-card:hover {
  opacity: 1;
  transform: scale(1.1);
}

.service-card {
  position: relative;
  cursor: default;
  transition: 0.25s;
  margin: 1rem 0;
  padding: 2rem;
  background: var(--color-input);
}

.service-card:hover .card-title::before {
  width: 3.5rem;
}

.service-card-icon {
  display: none;
  top: -0.75rem;
  left: 2rem;
  padding: 0 1rem;
  background-color: var(--color-body);
  position: absolute;
  z-index: 1;
}

.service-card-icon .icon {
  fill: var(--color-primary);
}

.service-card-content-badge {
  text-align: center;
  padding: 0.25rem 0.5rem;
  background: var(--color-container);
  color: var(--color-primary);
  font-size: 0.75rem;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.service-card-content-badges {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.service-card-content-badge-title {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 600;
  font-size: 0.925rem;
  color: var(--color-title);
}

.service-card-content-title {
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
}

.service-card-content-title::before {
  position: absolute;
  content: "";
  bottom: 0;
  width: 2rem;
  height: 2px;
  background-color: var(--color-primary);
  transition: 0.5s ease;
}

.service-card-content-desc {
  text-align: justify;
}

.skill-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(7rem, 1fr));
  grid-template-rows: auto;
  flex-wrap: wrap;
  grid-gap: 0.35rem;
  gap: 0.35rem;
  margin-bottom: 3rem;
}

.skill-list:hover .skill-card {
  opacity: 0.7;
}

.skill-list .skill-card:hover {
  opacity: 1;
  transform: scale(1.1);
}

.skill-card:hover .highlight {
  animation: animation-highlight 1s linear;
}

.skill-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 7rem;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  transition: 0.25s;
}

.skill-card::before {
  content: "";
  position: absolute;
  top: 1%;
  left: 1%;
  display: block;
  height: 98%;
  width: 98%;
  margin: auto;
  align-self: center;
  background: var(--color-input);
  z-index: -1;
}

.skill-card-label {
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  margin: 0.5rem;
  font-weight: 500;
  font-size: 0.825rem;
  color: var(--color-title);
  z-index: 1;
  cursor: default;
  text-align: center;
}

.skill-card .icon {
  fill: var(--color-primary);
  z-index: 1;
  cursor: auto;
  cursor: initial;
}

.highlight {
  position: absolute;
  top: -50%;
  left: -50%;
  z-index: -9;
  display: block;
  height: 200%;
  width: 200%;
  transform: rotate(-60deg);
  overflow: hidden;
  background: linear-gradient(
    to right,
    var(--color-body) 0%,
    var(--color-body) 40%,
    var(--color-primary) 50%,
    var(--color-primary) 55%,
    var(--color-body) 70%,
    var(--color-body) 100%
  );
  background-size: 200% auto;
}

@keyframes animation-highlight {
  to {
    background-position: 200% center;
  }
}

.form-field {
  margin-bottom: 1rem;
}

input,
textarea {
  resize: none;
  border: 1px solid transparent;
  box-sizing: border-box;
  outline: none;
  width: 100%;
  padding: 1rem;
  margin: 0.25rem 0;
  font-family: var(--body-font);
  color: var(--color-title);
  background-color: var(--color-input);
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  letter-spacing: 1px;
  color: var(--color-text-light);
}

input::placeholder,
textarea::placeholder {
  letter-spacing: 1px;
  color: var(--color-text-light);
}

.form-field-label {
  align-items: baseline;
  display: inline-flex;
}

.form-field-label label {
  text-transform: uppercase;
  font-size: 0.825rem;
  font-weight: 600;
  margin-right: 0.5rem;
  color: var(--color-title);
  letter-spacing: 2px;
}

.input-error-field {
  border: 1px solid var(--color-primary);
}

.input-error-message {
  letter-spacing: 1px;
  font-size: 0.825rem;
  color: var(--color-primary);
}

.contact-form-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 0.5rem;
  transform: translate(-50%, -50%) !important;
  margin: auto auto;
  background-color: var(--color-body);
  outline: none;
}

.contact-form-modal-content {
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.contact-form-modal-content-row {
  display: flex;
  align-items: center;
}

.contact-form-modal-content-icon {
  margin: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  background: var(--color-primary);
  font-size: 2rem;
  color: var(--color-title);
  font-weight: 600;
}

.contact-form-modal-content-icon .icon,
.contact-form-modal-content-icon .icon:hover {
  fill: var(--color-title);
  cursor: auto;
  cursor: initial;
}

.contact-form-modal .icon-close {
  height: 1.75rem;
  float: right;
}

@media screen and (max-width: 767px) {
  .contact-form-modal {
    width: 100%;
    height: 100%;
  }

  .icon-close {
    margin: 1rem 1rem 0 0;
  }
}

.loader {
  width: 3rem;
  height: 0.75rem;
  margin-right: 1rem;
  position: relative;
}

.loader-square {
  width: 0.75rem;
  height: 0.75rem;
  background-color: var(--color-primary);
  position: absolute;
  animation: pulse 0.5s ease-in-out infinite alternate;
}

.square-1 {
  left: 0;
  transform-origin: 100% 50%;
}

.square-2 {
  left: 50%;
  transform: translateX(-50%) scale(1);
  animation-delay: 0.25s;
}

.square-3 {
  right: 0;
  animation-delay: 0.5s;
}

@keyframes pulse {
  to {
    transform: translateX(-50%) scale(0);
  }
}



.accordion-item {
  background: var(--color-input);
  margin: 0 0 1rem;
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
}

.accordion-item-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: 0.25s ease;
  cursor: pointer;
}

.accordion-item-label .icon {
  margin-left: 1rem;
  transform: rotate(180deg);
}

.accordion-item-active .icon {
  transition: 0.5s ease;

  transform: rotate(0deg);
}

.accordion-item-label-headline {
  color: var(--color-title);
}

.accordion-item-text {
  margin: 0 2rem 0 0;
  max-height: 0;
  overflow: hidden;
  text-align: justify;
  font-size: 0.925rem;
  transition: max-height 0.5s;
}

.accordion-item-text-open {
  transition: max-height 1s ease-in-out;
  max-height: 100vh;
}

.accordion {
  list-style: none;
  transition: 0.5s ease;
}


.icon {
  height: 1.5rem;
  fill: var(--color-text);
  cursor: pointer;
}

.icon:hover {
  fill: var(--color-primary);
}

.nav {
  display: flex;
  font-size: 0.75rem;
  font-weight: 500;
  color: var(--color-title);
  height: 100%;
}

.nav-left {
  display: flex;
  align-items: center;
}

.night-mode-switch {
  display: flex;
}

.nav-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.nav-item {
  padding: 0.5rem 0;
  transition: 0.5s;
  display: flex;
}

.dash {
  width: 4.5rem;
  height: 2px;
  margin: 1rem;
  background-color: var(--color-title);
  transition: 0.35s;
}

.number {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-input);
  font-size: 0.75rem;
  font-weight: 500;
  width: 2.25rem;
  height: 2.25rem;
}

.nav-link {
  color: var(--color-title);
  display: flex;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.nav-link:hover {
  color: var(--color-primary);
}

.nav-link:hover .dash {
  width: 7rem;
  background-color: var(--color-primary);
}

.active .dash {
  width: 7rem;
  background-color: var(--color-primary);
}

.active a {
  color: var(--color-primary);
}

.nav-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 1rem;
  right: 2rem;
  width: 3rem;
  height: 3rem;
  background-color: var(--color-primary);
  z-index: 2;
}

.nav-toggle-dash {
  width: 1.5rem;
  height: 2px;
  background: black;
  position: relative;
  transition: 0.35s ease-in-out;
}

.nav-toggle-dash::after,
.nav-toggle-dash::before {
  position: absolute;
  content: "";
  width: 1rem;
  height: 2px;
  background: black;
  transition: 0.35s ease-in-out;
}

.nav-toggle-dash::before {
  top: -8px;
  right: 0.125rem;
}

.nav-toggle-dash::after {
  top: 8px;
  left: 0.125rem;
}

.nav-toggle.close .nav-toggle-dash {
  background: transparent;
}

.nav-toggle.close .nav-toggle-dash::before {
  transform: rotate(45deg) translate(4px, 8px) scale(1.5, 1);
}

.nav-toggle.close .nav-toggle-dash::after {
  transform: rotate(-45deg) translate(6px, -4px) scale(1.5, 1);
}

@media screen and (max-width: 954px) {
  .nav-menu {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 100%;
    padding: 3rem;
    transition: 0.5s ease-in-out;
    z-index: 2;
    background-color: var(--color-body);
  }

  .nav-list {
    align-items: center;
  }

  .nav-item {
    margin: 1rem;
  }

  .nav-link span {
    width: 6rem;
    font-size: 0.925rem;
    text-align: center;
    position: relative;
    background: var(--color-body);
  }

  .nav-item.active .nav-link span::before {
    position: absolute;
    content: "";
    top: 50%;
    z-index: -1;
    left: -2.75rem;
    width: 12rem;
    height: 1px;
    background: var(--color-primary);
  }

  .dash,
  .number {
    display: none;
  }

  .show-menu {
    left: 0;
    z-index: 2;
  }
}

@media screen and (min-width: 954px) {
  .nav-toggle,
  .nav-close {
    display: none;
  }
}

.progress-bar {
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  width: 2px;
  height: 50%;
  background: linear-gradient(60deg, var(--color-body), var(--color-primary));
  border-radius: 8px;
}

.progress-bar::before {
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  left: -3px;
  top: 0;
  z-index: 1;
  transform: rotate(45deg);
  background-color: var(--color-primary);
}

.progress-bar-pulse {
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  left: -3px;
  top: 0;
  opacity: 0;
  border: 1px solid var(--color-primary-light);
  animation: pulsate 1s 0.35s ease-out;
  animation-iteration-count: 3;
}

.progress-bar-percent {
  transition: 0.3s;
  left: 0;
  width: 2px;
  border-radius: 8px;
  background: linear-gradient(
    60deg,
    var(--color-primary-light),
    var(--color-body)
  );
  position: absolute;
  bottom: 100%;
}

@keyframes pulsate {
  0% {
    transform: rotate(45deg) scale(0.1, 0.1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: rotate(45deg) scale(3, 3);
    opacity: 0;
  }
}

@media screen and (max-width: 954px) {
  .progress-bar {
    display: none;
  }
}

.color-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) !important;
  margin: auto auto;
  background-color: var(--color-body);
  outline: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.color-modal-title {
  margin: 1rem 0 0;
}

.color-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(2.25rem, 1fr));
  align-items: center;
  justify-content: center;
  grid-gap: 0.75rem;
  gap: 0.75rem;
  flex-wrap: wrap;
  max-width: 30rem;
  padding: 2.5rem 5rem;
}

.color-item {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 2.25rem;
  height: 2.25rem;
  transition: 0.5s ease;
  overflow: hidden;
  position: relative;
}

.color-item:hover {
  transform: scale(1.5);
}

.color-item .icon {
  fill: black;
  width: 1rem;
  height: 1rem;
}

.color-modal .icon-close {
  height: 1.75rem;
  position: absolute;
  top: 1rem;
  right: 1.5rem;
}

@media screen and (max-width: 767px) {
  .color-modal {
    width: 100%;
    height: 100%;
  }
}

.color-switch {
  transition: 0.35s ease;
  background: var(--color-input);
  overflow: hidden;
  animation: fade-in 1.5s ease;
}

.color-switch:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.color-switch .icon {
  animation: day-shift 0.5s ease;
}

.color-switch:hover .icon {
  fill: var(--color-primary);
}

.color-switch-header {
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.night-mode-switch {
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: var(--color-input);
  transition: 1s ease;
  overflow: hidden;
  animation: fade-in 1.5s ease;
}

.night-mode-switch:hover {
  transform: scale(1.1);
}

.night-mode-switch:hover .icon {
  fill: var(--color-primary);
}

.night-mode-switch .icon {
  animation: day-shift 0.5s ease;
}

.footer {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.footer-image {
  width: 5.5rem;
  height: 5.5rem;
  background: var(--color-input);
}

.footer-content {
  display: flex;
}

.footer-links {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.footer-link {
  display: flex;
  align-items: center;
  font-size: 0.825rem;
  letter-spacing: 1px;
  color: var(--color-text);
}

.footer-link-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  margin: 0 0.5rem;
  background: var(--color-input);
}

.footer-link:hover,
.footer-link:hover svg {
  fill: var(--color-primary);
  color: var(--color-primary);
  cursor: pointer;
}

.footer-actions {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
}

.top-shadow {
  position: fixed;
  left: 0;
  height: 10vh;
  z-index: 2;
  width: 100%;
  background: linear-gradient(var(--color-body), transparent);
}

.container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  max-width: var(--width-desktop);
}

.column-right {
  flex: 1 1;
  padding: 5vh 2rem;
  position: relative;
}

.column-left {
  top: 0;
  padding: 5vh 2rem;
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  justify-content: space-between;
  flex: 1 1;
  max-height: 100vh;
  flex-direction: column;
  align-items: flex-start;
}

.typewriter-headline {
  font-size: 2.75rem;
  margin: 0;
  color: var(--color-primary);
}

.title {
  margin: 0;
  font-size: 2.5rem;
}

.desc {
  text-align: justify;
  line-height: 2;
}

@media screen and (max-width: 954px) {
  .container {
    flex-direction: column;
  }
  .column-left {
    position: relative;
    max-height: none;
  }
}

.App {
  max-width: var(--width-desktop);
  margin: auto;
  display: flex;
  min-height: 100%;
  background: linear-gradient(var(--color-input), var(--color-input))
      calc(1 * 100% / 6),
    linear-gradient(var(--color-input), var(--color-input)) calc(2 * 100% / 6),
    linear-gradient(var(--color-input), var(--color-input)) calc(3 * 100% / 6),
    linear-gradient(var(--color-input), var(--color-input)) calc(4 * 100% / 6),
    linear-gradient(var(--color-input), var(--color-input)) calc(5 * 100% / 6);
  background-size: 1px 100%;
  background-repeat: no-repeat;
}

.headline {
  font-size: 1.125rem;
  font-weight: 400;
  letter-spacing: 3px;
  color: var(--color-title);
  text-transform: uppercase;
  position: relative;
}

.card-title {
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
  margin-top: 0;
}

.card-title::before {
  position: absolute;
  content: "";
  bottom: -2px;
  width: 2rem;
  height: 2px;
  background-color: var(--color-primary);
  transition: 0.5s ease;
}

.primary {
  color: var(--color-primary);
}

@keyframes day-shift {
  from {
    transform: translateY(2rem);
  }

  to {
    transform: translateY(0rem);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

